// Mixin
//// ------------->
@mixin bg {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat; }
@mixin flex_center {
  display: flex;
  justify-content: center;
  align-items: center; }
@mixin size($w,$h:$w) {
  width: $w;
  height: $h; }
@mixin poa($t,$l:$t) {
  top: $t;
  left: $l;
  position: absolute;
  transform: translate(-50%,-50%); }

// Theme
//// ------------->
* {
  // border: solid 1px #888 !important
 }  // box-sizing: border-box
a {
  text-decoration: none;
  box-sizing: border-box; }
.clearfix {
  clear: both; }
.wrap {
  width: 1250px;
  margin: 0 auto;
  position: relative;
  box-sizing: border-box; }
#app {
  font-family: Microsoft JhengHei;
  .bannerArea {
    height: 300px;
    background: #eee; }
  header {
    position: relative;
    width: 100%;
    z-index: 9999;
    background-color: #fff;
    border-bottom: 1px solid #e5e5e5;
    .logo {
      @include poa(50%);
      width: 150px;
      text-align: center; }
    .menu {
      li {
        width: 160px;
        display: inline-block;
        text-align: center;
        position: relative;
        transition: .5s;
        &:hover {
          background: #ddd;
          a {
            color: #b7985b; } }
        a {
          font-weight: 900;
          position: relative;
          font-size: 15px;
          color: #333;
          letter-spacing: 1px;
          line-height: 100px;
          transition: .3s; }
        &:before {
          content: '';
          position: absolute;
          left: 0;
          top: 50%;
          transform: translateY(-50%);
          width: 1px;
          height: 17px;
          background-color: #dfdfdf; } }
      li:nth-child(3) {
        margin-right: 250px;
        &:after {
          content: '';
          position: absolute;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
          width: 1px;
          height: 17px;
          background-color: #dfdfdf; } } } }
  .mainArea {
    padding: 90px 0 0;
    background: none;
    .productArea {
      position: relative;
      margin-bottom: 100px;
      .titleBox {
        position: relative;
        margin-bottom: 80px;
        text-align: center;
        color: #000;
        h2 {
          font-size: 30px;
          letter-spacing: 2px;
          font-weight: 900;
          position: relative;
          margin-bottom: 60px;
          span {
            font-weight: 500;
            font-size: 12px;
            @include poa(-20px, 50%); } }
        .note {
          line-height: 1.8;
          a {
            color: #000; } } }
      .productList {
        .post {
          float: left;
          width: 50%;
          margin-bottom: 30px;
          .item {
            display: flex;
            align-items: center;
            .img {
              float: left;
              width: 50%;
              box-sizing: border-box;
              padding: 0 20px;
              img {
                width: 100%;
                position: relative;
                &:before {
                  @include size(100%);
                  @include poa(50%);
                  content: '';
                  z-index: 10;
                  background-color: rgba(210, 178, 129, 0.9);
                  border-radius: 0;
                  opacity: 1;
                  pointer-events: none;
                  transition: all .5s ease-in-out; } } }
            .text {
              float: left;
              width: 50%;
              box-sizing: border-box;
              padding: 0 20px;
              color: #333;
              &:hover h3 {
                  color: #b7985b; }
              h3 {
                font-size: 20px;
                font-weight: 900;
                transition: .3s;
                margin-bottom: 20px; }
              p {
                font-size: 14px;
                line-height: 2;
                letter-spacing: .05em; } } } } } }
    .aboutArea {
      .title {
        margin-bottom: 100px;
        h2 {
          font-size: 30px;
          letter-spacing: 2px;
          font-weight: 900;
          position: relative;
          text-align: center;
          position: relative;
          span {
            font-weight: 500;
            font-size: 12px;
            @include poa(-20px, 50%); } } }
      .list {
        .post {
          margin-bottom: 50px;
          .item {
            display: flex;
            align-items: center;
            .img {
              float: left;
              width: 50%;
              img {
                width: 100%; } }
            .text {
              float: left;
              width: 50%;
              box-sizing: border-box;
              padding: 0 50px;
              h3 {
                position: relative;
                font-size: 26px;
                display: inline-block;
                margin-bottom: 50px;
                color: #333;
                font-weight: 900;
                &:before {
                  margin-bottom: 5px;
                  content: 'New Adventure New Land';
                  display: block;
                  font-size: 12px;
                  color: #888; } }
              p {
                font-weight: 900;
                font-size: 14px;
                line-height: 2;
                letter-spacing: 2px;
                color: #333; }
              .btn_more {
                margin-top: 50px; } } } } } }
    .processArea {
      padding: 50px 0 150px 0;
      background: url('https://www.dahuandesign.com/images/processBg.jpg');
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center center;
      text-align: center;
      .title {
        text-align: center;
        margin-bottom: 50px;
        h2 {
          font-size: 12px;
          color: #fff;
          position: relative;
          span {
            margin-top: 5px;
            font-size: 34px;
            letter-spacing: 2px;
            display: block; } } }
      ul {
        margin-top: 80px;
        text-align: center;
        li {
          width: 16%;
          display: inline-block;
          .img {
            @include size(150px);
            position: relative;
            z-index: 0;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 35px;
            border-radius: 50%;
            background-color: #fff;
            transition: all .3s;
            &:before {
              @include poa(50%);
              @include size(180px);
              content: '';
              z-index: -1;
              background-color: rgba(255, 255, 255, 0.15);
              border-radius: 50%; }
            &:after {
              @include poa(50%);
              @include size(110%);
              content: '';
              z-index: -1;
              background-color: rgba(255, 255, 255, 0.15);
              border-radius: 50%;
              opacity: 0; }
            img {
              display: block;
              max-width: 100%;
              transition: all .5s; } }
          .text {
            position: relative;
            display: block;
            font-weight: 300;
            font-size: 20px;
            letter-spacing: .1em;
            color: #fff;
            text-align: center;
            p {
              &:after {
                @include size(9px, 16px);
                content: '';
                position: absolute;
                right: 0px;
                top: 50%;
                transform: translateY(-50%);
                background: url(https://www.dahuandesign.com/images/icon_arrow.png) no-repeat; } } } } }
      .btn_more {
        margin-top: 100px;
        color: #fff;
        border-color: #fff;
        font-size: 18px;
        padding: 10px 30px; } }
    .newsArea {
      transform: translateY(-70px);
      ul {
        li {
          float: left;
          width: 33%;
          box-sizing: border-box;
          padding: 0 20px;
          .img {
            img {
              width: 100%; } }
          .title {
            position: relative;
            z-index: 1;
            width: calc(100% - 120px);
            margin-top: -80px;
            padding: 20px 10px 20px 15px;
            background-color: #fff;
            em {
              font-size: 14px;
              color: #666; }
            h3 {
              margin-top: 5px;
              font-size: 20px;
              color: #333;
              font-weight: 900;
              white-space: nowrap; } }
          .text {
            margin-top: 20px;
            padding-left: 15px;
            p {
              height: 50px;
              font-weight: 300;
              font-size: 14px;
              color: #444;
              line-height: 1.8;
              letter-spacing: 1px; } } } } }
    .winnerArea {
      padding: 100px 0;
      background: url('https://www.dahuandesign.com/images/bg.jpg');
      .top {
        .leftItem {
          float: left;
          width: 50%;
          ul {
            li {
              float: left;
              width: 33%;
              height: 150px;
              img {} } } }
        .rightItem {
          float: left;
          width: 50%;
          box-sizing: border-box;
          padding-left: 50px;
          .title {
            margin-bottom: 50px;
            em {
              font-size: 12px;
              color: #888; }
            h2 {
              margin-top: 5px;
              font-size: 26px;
              font-weight: 900;
              color: #333;
              letter-spacing: 4px; } }
          .text {
            p {
              margin-bottom: 20px;
              line-height: 2.2;
              font-weight: 900;
              color: #333; } } } } } }
  footer {
    padding: 100px 0 170px;
    .itemLeft {
      float: left;
      width: 60%;
      .icon {
        @include size(105px);
        position: relative;
        display: inline-block;
        border-radius: 50%;
        background: url(https://www.dahuandesign.com/images/icon_home.png) no-repeat center center;
        &:before {
          @include size(100%);
          @include poa(50%);
          content: '';
          border: 1px solid #e5e5e5;
          border-radius: 50%; }
        &:after {
          @include size(97px);
          @include poa(50%);
          content: '';
          border: 1px solid #d2d2d2;
          border-radius: 50%; } }
      .text {
        padding-left: 30px;
        display: inline-block;
        color: #333;
        transform: translateY(-10px);
        span {
          font-size: 14px;
          letter-spacing: 2px; }
        .tel {
          font-size: 33px;
          margin: 15px 0;
          div {
            display: inline-block; }
          em {
            padding-left: 20px;
            color: #cc9900;
            display: inline-block; } }
        address {
          font-size: 14px;
          margin-top: 5px; } } }
    .itemRight {
      float: left;
      width: 40%;
      position: relative;
      box-sizing: border-box;
      padding-left: 50px;
      p {
        transform: translateY(-10px);
        line-height: 2;
        color: #333333;
        font-weight: 900; }
      &:before {
        @include poa(45%, -35px);
        @include size(1px, 100%);
        content: '';
        background: #eee; } } } }
// Button
//// ----------->
.btn_more {
  border: solid 1px #cc9900;
  color: #cc9900;
  display: inline-block;
  font-size: 24px;
  position: relative;
  padding: 5px 10px;
  margin: 30px 0;
  transition: .3s;
  cursor: pointer;
  &:hover {
    background: #cc9900;
    color: #fff; } }

